.navbar {
  height: 65px;
  border-bottom: 0.5px solid rgb(231, 228, 228);
  display: flex;
  flex-wrap: initial;
  align-items: center;
  font-size: 14px;
  // background-color: rgba(239, 239, 239, 0.205);
  color: #555;
  box-shadow: 2px 4px 10px 1px rgba(201, 201, 201, 0.47);

  .wrapper {
    width: 100%;
    padding: 20px;
    display: flex;
    align-items: center;
    justify-content: space-between;

    .search {
      display: flex;
      align-items: center;
      // border: 0.5px solid lightgray;
      padding: 3px;
    
    .logo {
      width: 50px;
      height: 50px;
    }

      input {
        border: none;
        outline: none;
        background: transparent;

        &::placeholder {
          font-size: 12px;
        }
      }
    }

    .items {
      display: flex;
      align-items: center;

      .item {
        display: flex;
        align-items: center;
        margin-right: 20px;
        position: relative;

        .icon {
          font-size: 20px;
        }

        .avatar {
          width: 30px;
          height: 30px;
          border-radius: 50%;
        }

        .counter {
          width: 15px;
          height: 15px;
          background-color: red;
          border-radius: 50%;
          color: white;
          display: flex;
          align-items: center;
          justify-content: center;
          font-size: 10px;
          font-weight: bold;
          position: absolute;
          top: -5px;
          right: -5px;
        }
      }
    }
  }
}
