.datatable{height:auto;padding:20px}.datatable .datatableTitle{width:100%;font-size:24px;color:gray;margin-bottom:10px;display:flex;align-items:center;justify-content:space-between}.datatable .datatableTitle .link{text-decoration:none;color:#800062;font-size:16px;font-weight:400;border:1px solid #800057;padding:5px;border-radius:5px;cursor:pointer}.datatable .cellWithImg{display:flex;align-items:center}.datatable .cellWithImg .cellImg{width:32px;height:32px;border-radius:50%;object-fit:cover;margin-right:20px}.datatable .cellWithStatus{padding:5px;border-radius:5px}.datatable .cellWithStatus.Validé{background-color:rgba(0,128,0,.05);color:green}.datatable .cellWithStatus.Broullon{background-color:rgba(255,217,0,.05);color:#daa520}.datatable .cellWithStatus.Annulé{background-color:rgba(255,0,0,.05);color:crimson}.datatable .cellAction{display:flex;align-items:center;gap:15px}.datatable .cellAction .viewButton{padding:2px 5px;border-radius:5px;color:#00008b;border:1px dotted rgba(0,0,139,.596);cursor:pointer}.datatable .cellAction .deleteButton{padding:2px 5px;border-radius:5px;color:crimson;border:1px dotted rgba(220,20,60,.6);cursor:pointer}.datatable .submitButton{width:200px;height:30px;border:none;background-color:green;color:#fff;font-weight:bold;cursor:pointer;margin-right:10px}.datatable .openOdooButton{width:200px;height:30px;border:none;background-color:#6d4964;color:#fff;font-weight:bold;cursor:pointer;margin-right:10px}.datatable input[type=file]{width:200px;height:30px;border:none;color:#6d6d6d;font-weight:bold;cursor:pointer;margin-right:10px}